<template>
  <div>
    <!-- 头部 -->
    <nav-bar class="home-nav">
      <div slot="left" class="left" @click="$router.go(-1)">
        <img src="~assets/img/common/back.svg" />
      </div>
      <div slot="center" class="header">
        <img src="@/styles/img/4、微网站搜索_03.png" alt />
      </div>
    </nav-bar>

    <layout-search ></layout-search>
    <section>
    <goods-list :goods="goods"></goods-list>
    </section>
  </div>
</template>

<script>
import NavBar from "components/common/navbar/NavBar";
import LayoutSearch from "components/common/layoutSearch/LayoutSearch";
import GoodsList from "components/common/goodsList/GoodsList";
export default {
  name: "List",
  data() {
    return {
      page: 1,
      pageSize: 10,
      goods: []
    };
  },
  components: {
    NavBar,
    LayoutSearch,
    GoodsList
  },
  created() {},
  mounted() {
    // 获取到分类列表传过来的对应的商品tid
    let tid = this.$route.query.tid;
    // console.log(tid);
    this.fetchGoodsList(tid);
  },
  methods: {
    // 传入tid 获取对应的商品数据
    async fetchGoodsList(tid) {
      let res = await this.$http.get(this.$api.GOODS, {
        state:1,
        tid,
        page: this.page,
        pageSize: this.pageSize
      });
      this.goods = res.data.data;
    }
  }
};
</script>
<style lang='scss' scoped>
@import "styles/list";
.header img {
  width: 1.94rem;
  height: 0.4rem;
  margin-top: 0.24rem;
}
.left {
  padding-top: 5px;
  padding-left: 5px;
}
</style>
